import { Text, Avatar, Box, useMantineTheme } from '@mantine/core'
import { useShallowEffect } from '@mantine/hooks'
import { DataTable } from 'mantine-datatable'
import { getValue } from '../../../../utils/data/v0.2.0/data'
import { useState } from 'react'

export default function Table_v0_1_0(props: any) {
  const { dbClass, tableData, columns, isLoading, selectable, selectableType, highlightSelectedRow } = props

  const [selectedId, setSelectedId] = useState('')
  const enableOnRowClick = selectable === true && selectableType === 'singleRow'

  // defaults
  useShallowEffect(() => {
    setSelectedId(tableData?.[dbClass]?.[0]?.id)
    props.selectedItem(tableData?.[dbClass]?.[0])
  }, [dbClass])

  const theme = useMantineTheme()

  const ColumnRender = (row: any, _: any, accessor: any) => {
    const columnProps = columns.find((c: { accessor: any }) => c.accessor === accessor)
    const dataType = columnProps.dataType
    const fieldFormat = columnProps.fieldFormat || accessor
    let value: string | undefined = getValue(row, fieldFormat)

    switch (dataType) {
      case 'date': value = window.Dayjs(value).format(columnProps.dateFormat); break
    }
    const columnRender = columnProps.columnRender

    switch (columnRender) {
      case 'avatar':
        return value ? <Avatar m={-6} color="red.4" variant="filled" radius="xl" >{value}</Avatar> : <></>
      case 'cycle':
        return value ? <Box
          sx={(theme) => ({
            height: 16,
            width: 16,
            borderRadius: 8,
            backgroundColor: theme.colors[columnProps.dataMap[value || 'dark']][6],
          })} /> : <></>
      default: return <Text >{value}</Text>
    }
  }

  return (
    <DataTable
      columns={columns}
      records={tableData?.[dbClass] || []}
      fetching={isLoading}
      loaderVariant='dots'
      noRecordsText={null}
      defaultColumnRender={ColumnRender}
      sx={{ background: 'white', width: props.widthString, height: props.heightString }}
      onRowClick={enableOnRowClick && ((data: any) => {
        setSelectedId(data.id)
        props.selectedItem(data)
        props.selected()
      })}
      rowStyle={({ id }) => (
        enableOnRowClick && selectedId === id && highlightSelectedRow
          ? { backgroundColor: theme.colors.red[3] }
          : undefined
      )}
      {...props}
    />
  )
}